import React, { useState } from "react";
// material-ui
import {
  Grid,
  Divider,
  Box,
  Button,
  Container,
  Input,
  InputAdornment,
  Typography,
  TextField,
} from "@material-ui/core";
import { TextareaAutosize } from "@material-ui/core";
import MuiTypography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/styles";
import { useSelector } from "react-redux";

// project imports
import SubCard from "../../../ui-component/cards/SubCard";
import MainCard from "../../../ui-component/cards/MainCard";
import { gridSpacing } from "../../../store/constant";

import AudiotrackRoundedIcon from "@material-ui/icons/AudiotrackRounded";
import AssignmentIcon from "@material-ui/icons/Assignment";
import OndemandVideoRoundedIcon from "@material-ui/icons/OndemandVideoRounded";
import TelegramIcon from "@material-ui/icons/Telegram";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";
import CheckCircleRoundedIcon from "@material-ui/icons/CheckCircleRounded";
import AnimateButton from "../../../ui-component/extended/AnimateButton";
import UploadButton from "../../../ui-component/UploadButton";
import { sendDatatoServer } from "../../../utils";

const useStyles = makeStyles((theme) => ({
  root: {
    fontFamily: theme.fontFamily,
  },
  input: {
    display: "none",
  },
  center: {
    textAlign: "center",
    justifyContent: "center",
    alignItems: "center",
  },
  button: {
    cursor: "pointer",
    textAlign: "center",
    userSelect: "none",
  },
  signDivider: {
    flexGrow: 1,
  },
  textArea: {
    borderRadius: "4px",
    width: "100%",
    borderColor: "grey",
  },
}));

//==============================|| ASYNC ||==============================//

const Intro = (props) => {
  const classes = useStyles();

  const { toggleShowStatus } = props;
  return (
    <SubCard>
      <Grid container direction="column" spacing={1} className={classes.center}>
        <Grid item>
          <MuiTypography variant="h1" gutterBottom>
            Modzy API
          </MuiTypography>
          <MuiTypography sx={{ fontSize: 20 }}>
            Create conversations from text like emails / conversations{" "}
            <AssignmentIcon sx={{ fontSize: 15 }} /> audio files{" "}
            <AudiotrackRoundedIcon sx={{ fontSize: 15 }} /> or video files{" "}
            <OndemandVideoRoundedIcon sx={{ fontSize: 15 }} />.
          </MuiTypography>
        </Grid>
        <Grid item>
          <Container className={classes.button}>
            <br />
            <br />
            <Button
              variant="contained"
              color="secondary"
              className={classes.button}
              onClick={() => toggleShowStatus()}
              endIcon={<CheckCircleRoundedIcon style={{ fontSize: 20 }} />}
            >
              Check Status
            </Button>
            <br />
            <br />
          </Container>
        </Grid>
      </Grid>
    </SubCard>
  );
};

const UserForm = (props) => {
  const classes = useStyles();
  const customization = useSelector((state) => state.customization);
  return (
    <SubCard>
      <Grid container direction="row" spacing={1}>
        <Grid item xs={12}>
          <Typography variant="h3" className={classes.center}>
            {props.element.toUpperCase()}
          </Typography>
          <br />
          <SubCard className={classes.center}>
            <>
              <UploadButton {...props} /> {props.file && props.file.name ? props.file.name : ""}
            </>
          </SubCard>
        </Grid>

        <Grid item xs={12}>
          <Box
            sx={{
              alignItems: "center",
              display: "flex",
            }}
          >
            <Divider className={classes.signDivider} orientation="horizontal" />
            <AnimateButton>
              <Button
                variant="outlined"
                className={classes.signText}
                sx={{ borderRadius: customization.borderRadius + "px" }}
                disableRipple
                disabled
              >
                OR
              </Button>
            </AnimateButton>
            <Divider className={classes.signDivider} orientation="horizontal" />
          </Box>
        </Grid>
        <Grid item xs={12}>
          <SubCard className={classes.center}>
            {props.element === "text" ? (
              <TextareaAutosize
                value={props.link}
                className={classes.textArea}
                onChange={(e) => props.setLink(e.target.value)}
                placeholder={"Paste the content here.."}
                minRows={10}
              />
            ) : (
              <Input
                id="url"
                fullWidth
                value={props.link}
                onChange={(e) => props.setLink(e.target.value)}
                startAdornment={
                  <InputAdornment position="start">
                    <LinkRoundedIcon />
                  </InputAdornment>
                }
              />
            )}
          </SubCard>
        </Grid>

        <Grid item xs={12} marginTop={5}>
          <Container className={classes.button}>
            <Button
              variant="contained"
              color="primary"
              className={classes.button}
              onClick={props.handleSend}
              endIcon={<TelegramIcon style={{ fontSize: 20 }} />}
            >
              Send
            </Button>
          </Container>
        </Grid>
      </Grid>
    </SubCard>
  );
};

const InputCard = (props) => {
  const classes = useStyles();
  const { setElement, name, setName } = props;
  return (
    <SubCard>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <AnimateButton>
            <SubCard className={classes.button} onClick={() => setElement("text")}>
              <AssignmentIcon style={{ fontSize: 50 }} />
              <br />
              Text / Conversations
            </SubCard>
          </AnimateButton>
        </Grid>
        <Grid item xs={12} sm={6}>
          <AnimateButton>
            <SubCard
              className={classes.button}
              onClick={() => {
                setElement("audio");
              }}
            >
              <AudiotrackRoundedIcon style={{ fontSize: 50 }} />
              <br />
              Audio
            </SubCard>
          </AnimateButton>
        </Grid>
        <Grid item xs={12} sm={6}>
          <AnimateButton>
            <SubCard className={classes.button} onClick={() => setElement("video")}>
              <OndemandVideoRoundedIcon style={{ fontSize: 50 }} />
              <br />
              Video
            </SubCard>
          </AnimateButton>
        </Grid>
        <Grid item xs={12}>
          <SubCard className={classes.button} onClick={() => setElement("video")}>
            <br />
            <TextField
              fullWidth
              label="Enter job name"
              variant="standard"
              required
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </SubCard>
        </Grid>
      </Grid>
    </SubCard>
  );
};

const AsyncSubmit = (props) => {
  const classes = useStyles();
  const { toggleShowStatus, addJobs, jobs } = props;

  const [element, setElement] = useState("text");
  const [name, setName] = useState("Untitled Job " + jobs.length);
  const [file, setFile] = useState("");
  const [link, setLink] = useState("");
  const handleSend = () => {
    sendDatatoServer({ name, file, link, type: element }).then((res) => {
      setName("Untitled Job " + (jobs.length + 1));
      setFile("");
      setLink("");
      setElement("text");
      if (res.data) {
        addJobs(res.data);
        toggleShowStatus();
      }
    });
  };

  return (
    <MainCard className={classes.root}>
      <Grid container spacing={gridSpacing}>
        <Grid item xs={12}>
          <Intro toggleShowStatus={toggleShowStatus} />
        </Grid>
        <Grid item xs={12} sm={6}>
          <InputCard name={name} setName={setName} setElement={setElement} />
        </Grid>
        <Grid item xs={12} sm={6}>
          <UserForm
            element={element}
            file={file}
            setFile={setFile}
            link={link}
            setLink={setLink}
            handleSend={handleSend}
          />
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default AsyncSubmit;
