import PropTypes from "prop-types";
import React from "react";

// material-ui
import { makeStyles } from "@material-ui/styles";
import {
  Avatar,
  Button,
  IconButton,
  CardActions,
  CardContent,
  Divider,
  Grid,
  Menu,
  MenuItem,
  Typography,
  Tooltip,
} from "@material-ui/core";

// project imports
import MainCard from "../../../ui-component/cards/MainCard";
import SkeletonJobStatus from "../../../ui-component/cards/Skeleton/JobStatus";
import { gridSpacing } from "../../../store/constant";

// assets
import ChevronRightOutlinedIcon from "@material-ui/icons/ChevronRightOutlined";
import MoreHorizOutlinedIcon from "@material-ui/icons/MoreHorizOutlined";
import CheckRoundedIcon from "@material-ui/icons/CheckRounded";
import TimerRoundedIcon from "@material-ui/icons/TimerRounded";
import CachedRoundedIcon from "@material-ui/icons/CachedRounded";
import AreaChartCard from "./AreaChartCard";

// style constant
const useStyles = makeStyles((theme) => ({
  none: {},
  cardAction: {
    padding: "10px",
    paddingTop: 0,
    justifyContent: "center",
  },
  primaryLight: {
    color: theme.palette.primary[200],
    cursor: "pointer",
  },
  divider: {
    marginTop: "12px",
    marginBottom: "12px",
  },
  avatarSuccess: {
    width: "16px",
    height: "16px",
    borderRadius: "5px",
    backgroundColor: "transparent",
    color: theme.palette.success.dark,
    marginRight: "15px",
  },
  successDark: {
    color: theme.palette.success.dark,
  },
  rotateIcon: {
    animation: "spin 4s linear infinite",
  },
  avatarError: {
    width: "16px",
    height: "16px",
    borderRadius: "5px",
    backgroundColor: "transparent",
    color: theme.palette.orange.dark,
    marginRight: "15px",
  },
  errorDark: {
    color: theme.palette.orange.dark,
  },
}));

//-----------------------|| DASHBOARD DEFAULT - POPULAR CARD ||-----------------------//

const JobStatus = (props) => {
  const { isLoading, jobs } = props;
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <React.Fragment>
      {isLoading ? (
        <SkeletonJobStatus />
      ) : (
        <MainCard content={false}>
          <CardContent>
            <Grid container spacing={gridSpacing}>
              <Grid item xs={12}>
                <Grid container alignContent="center" justifyContent="space-between">
                  <Grid item>
                    <Typography variant="h4">Check Job Status</Typography>
                  </Grid>
                  <Grid item>
                    <MoreHorizOutlinedIcon
                      fontSize="small"
                      className={classes.primaryLight}
                      aria-controls="menu-popular-card"
                      aria-haspopup="true"
                      onClick={handleClick}
                    />
                    <Menu
                      id="menu-popular-card"
                      anchorEl={anchorEl}
                      keepMounted
                      open={Boolean(anchorEl)}
                      onClose={handleClose}
                      variant="selectedMenu"
                      anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "right",
                      }}
                      transformOrigin={{
                        vertical: "top",
                        horizontal: "right",
                      }}
                    >
                      <MenuItem onClick={handleClose}> Today</MenuItem>
                      <MenuItem onClick={handleClose}> This Month</MenuItem>
                      <MenuItem onClick={handleClose}> This Year </MenuItem>
                    </Menu>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12} sx={{ pt: "16px !important" }}>
                <AreaChartCard />
              </Grid>
              <Grid item xs={12}>
                {jobs.map((e, i) => {
                  const completed = e.status ? e.status.textTopics : false;
                  return (
                    <div key={i}>
                      <Grid container direction="column">
                        <Grid item>
                          <Grid container alignItems="center" justifyContent="space-between">
                            <Grid item>
                              <Typography variant="h5" color="inherit">
                                {e.name ? e.name : e.itemId}
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Grid container alignItems="center" justifyContent="space-between">
                                <Typography variant="h6" color="inherit" marginRight={10}>
                                  {e.timestamp} &nbsp;
                                </Typography>
                                <Grid item>
                                  <Avatar
                                    variant="rounded"
                                    className={
                                      completed ? classes.avatarSuccess : classes.avatarError
                                    }
                                  >
                                    {completed ? (
                                      <Tooltip title="Completed">
                                        <CheckRoundedIcon fontSize="small" color="inherit" />
                                      </Tooltip>
                                    ) : (
                                      <Tooltip title="Pending">
                                        <TimerRoundedIcon fontSize="small" color="inherit" />
                                      </Tooltip>
                                    )}
                                  </Avatar>
                                </Grid>
                                <Grid item>
                                  <IconButton
                                    color="primary"
                                    className={isLoading ? classes.rotateIcon : classes.none}
                                  >
                                    <CachedRoundedIcon fontSize="small" color="inherit" />
                                    <style>{`@keyframes spin {
                 0% { transform: rotate(0deg); }
                 100% { transform: rotate(360deg); }
            }`}</style>
                                  </IconButton>
                                </Grid>
                              </Grid>
                            </Grid>
                          </Grid>
                        </Grid>
                        <Grid item>
                          <Typography variant="subtitle2" className={classes.successDark}>
                            {e.itemId}
                          </Typography>
                        </Grid>
                      </Grid>
                      {i !== jobs.length - 1 ? <Divider className={classes.divider} /> : ""}
                    </div>
                  );
                })}
              </Grid>
            </Grid>
          </CardContent>
          <CardActions className={classes.cardAction}>
            <Button size="small" disableElevation>
              View All
              <ChevronRightOutlinedIcon />
            </Button>
          </CardActions>
        </MainCard>
      )}
    </React.Fragment>
  );
};

JobStatus.propTypes = {
  isLoading: PropTypes.bool,
};

export default JobStatus;
