import React, { useEffect, useState } from "react";
import { Grid, Modal, Typography, Button, Container, Divider } from "@material-ui/core";
import FileCopyRoundedIcon from "@material-ui/icons/FileCopyRounded";
import AsyncSubmit from "./AsyncSubmit";
import JobStatus from "./JobStatus";
import { gridSpacing } from "../../../store/constant";
import SubCard from "../../../ui-component/cards/SubCard";
import { makeStyles } from "@material-ui/styles";
import { getItems, updateStatus } from "../../../utils";

const useStyles = makeStyles((theme) => ({
  paper: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    textAlign: "center",
    position: "absolute",
    maxWidth: 700,
    left: "50%",
    top: "40%",
    transform: "translate(-50%, -50%)",
  },
  center: {
    textAlign: "center",
  },
  text: {
    fontWeight: "bold",
    padding: 5,
  },
  button: {
    marginTop: 25,
  },
  divider: {
    marginTop: "12px",
    marginBottom: "12px",
  },
  gridItem: {
    minWidth: "700px",
  },
}));

//==============================|| ASYNC ||==============================//

function SendSucessModal({ open, handleClose }) {
  const classes = useStyles();
  const content = (
    <div>
      <SubCard className={classes.paper}>
        <Container>
          <Typography variant="h3">Details</Typography>
          <Divider className={classes.divider} />
          <Typography className={classes.text}>JobId: </Typography>
          <Typography className={classes.text}>ConversationId: </Typography>
        </Container>
        <Button
          className={classes.button}
          variant="contained"
          color="secondary"
          endIcon={<FileCopyRoundedIcon style={{ fontSize: 20 }} />}
        >
          Copy
        </Button>
      </SubCard>
    </div>
  );

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
    >
      {content}
    </Modal>
  );
}

const AsyncAPI = () => {
  const classes = useStyles();

  const [isLoading, setLoading] = useState(true);
  const [open, setOpen] = React.useState(false);
  const [showStatus, setShowStatus] = React.useState(false);
  const [jobs, setJobs] = useState([]);

  const setJobsUpdate = (data) => {
    setJobs(data);
    setTimeout(() => updateStatus(data).then((data) => setJobs(data)), 2000);
  };

  useEffect(() => {
    setLoading(false);
    getItems(setJobsUpdate);
  }, []);

  return (
    <>
      <SendSucessModal open={open} handleClose={() => setOpen(!open)} />
      <Grid container spacing={gridSpacing}>
        <Grid item xs className={classes.gridItem}>
          <AsyncSubmit
            isLoading={isLoading}
            toggleShowStatus={() => setShowStatus(!showStatus)}
            jobs={jobs}
            addJobs={(job) => setJobsUpdate([job, ...jobs])}
          />
        </Grid>
        <Grid item xs className={classes.gridItem} display={showStatus ? "" : "none"}>
          <JobStatus jobs={jobs} isLoading={isLoading} />
        </Grid>
      </Grid>
    </>
  );
};

export default AsyncAPI;
